:root {
  /* Color Themes */
  --red: hsl(1, 90%, 64%);
  --blue: hsl(219, 85%, 26%);
  --white: hsl(0, 0%, 100%);
  --very-light-grayish-blue: hsl(210, 60%, 98%);
  --light-grayish-blue-one: hsl(211, 68%, 94%);
  --light-grayish-blue-two: hsl(205, 33%, 90%);
  --grayish-blue: hsl(218, 14%, 63%);
  --dark-grayish-blue: hsl(219, 12%, 42%);
  --very-dark-blue: hsl(224, 21%, 14%);

  /* Gap */
  --gap-0: 0px;
  --gap-1: 1px;
  --gap-1_25: 0.125rem;
  --gap-2_5: 0.25rem;
  --gap-3_75: 0.375rem;
  --gap-5: 0.5rem;
  --gap-6_25: 0.625rem;
  --gap-7_5: 0.75rem;
  --gap-8_75: 0.875rem;
  --gap-10: 1rem;
  --gap-12_5: 1.25rem;
  --gap-15: 1.5rem;
  --gap-17_5: 1.75rem;
  --gap-20: 2rem;
  --gap-22_5: 2.25rem;
  --gap-27_5: 2.75rem;
  --gap-30: 3rem;
  --gap-35: 3.5rem;
  --gap-40: 4rem;
  --gap-50: 5rem;
  --gap-60: 6rem;
  --gap-70: 7rem;
  --gap-80: 8rem;
  --gap-90: 9rem;
  --gap-100: 10rem;
  --gap-110: 11rem;
  --gap-120: 12rem;
  --gap-130: 13rem;
  --gap-140: 14rem;
  --gap-150: 15rem;
  --gap-160: 16rem;
  --gap-180: 18rem;
  --gap-200: 20rem;
  --gap-240: 24rem;

  /* Display */
  --hidden: none;
  --block: block;
  --inline: inline;
  --inline-block: inline-block;
  --flex: flex;
  --inline-flex: inline-flex;
  --grid: grid;
  --inline-grid: inline-grid;

  /* Radius */
  --radius-1: 2px;
  --radius-2: 4px;
  --radius-3: 6px;
  --radius-4: 8px;
  --radius-5: 12px;
  --radius-6: 16px;
  --radius-7: 20px;
  --radius-8: 24px;
  --radius-9: 26px;
  --radius-10: 28px;
  --radius-11: 30px;
  --radius-12: 32px;
  --radius-13: 34px;
  --radius-14: 36px;
  --radius-15: 38px;
  --radius-16: 40px;

  /* Typography */
  --ff-plus-jakarta-sans-serif: var(--ff-plus-jakarta-sans), sans-serif;
  --ff-plus-jakarta-sans: "Plus Jakarta Sans";
  --fw-100: 100;
  --fw-200: 200;
  --fw-300: 300;
  --fw-400: 400;
  --fw-500: 500;
  --fw-600: 600;
  --fw-700: 700;
  --fw-800: 800;
  --line-height-none: 1;
  --line-height-tight: 1.25;
  --line-height-snug: 1.375;
  --line-height-normal: 1.5;
  --line-height-relaxed: 1.625;
  --line-height-loose: 2;
  --letter-spacing-tighter: -0.05em;
  --letter-spacing-tight: -0.025em;
  --letter-spacing-normal: 0em;
  --letter-spacing-wide: 0.025em;
  --letter-spacing-wider: 0.05em;
  --letter-spacing-widest: 0.1em;
  --font-feature-settings: "clig" off, "liga" off;
  --fs-1: clamp(0.875rem, 0.831rem + 0.1878vw, 1rem);
  --fs-2: clamp(1.3125rem, 1.2465rem + 0.2817vw, 1.5rem);

  /* Spacing */
  --space-1: clamp(0.25rem, 0.25rem + 0vw, 0.25rem);
  --space-2: clamp(0.4375rem, 0.4155rem + 0.0939vw, 0.5rem);
  --space-3: clamp(0.6875rem, 0.6655rem + 0.0939vw, 0.75rem);
  --space-4: clamp(0.875rem, 0.831rem + 0.1878vw, 1rem);
  --space-5: clamp(1.3125rem, 1.2465rem + 0.2817vw, 1.5rem);
  --space-6: clamp(1.75rem, 1.662rem + 0.3756vw, 2rem);
  --space-7: clamp(2.625rem, 2.493rem + 0.5634vw, 3rem);
  --space-8: clamp(3.5rem, 3.3239rem + 0.7512vw, 4rem);
  --space-9: clamp(5.25rem, 4.9859rem + 1.1268vw, 6rem);
  --space-10: clamp(0.25rem, 0.162rem + 0.3756vw, 0.5rem);
  --space-11: clamp(0.4375rem, 0.3275rem + 0.4695vw, 0.75rem);
  --space-12: clamp(0.6875rem, 0.5775rem + 0.4695vw, 1rem);
  --space-13: clamp(0.875rem, 0.6549rem + 0.939vw, 1.5rem);
  --space-14: clamp(1.3125rem, 1.0704rem + 1.0329vw, 2rem);
  --space-15: clamp(1.75rem, 1.3099rem + 1.8779vw, 3rem);
  --space-16: clamp(2.625rem, 2.1408rem + 2.0657vw, 4rem);
  --space-17: clamp(3.5rem, 2.6197rem + 3.7559vw, 6rem);
  --space-18: clamp(0.875rem, 0.4789rem + 1.6901vw, 2rem);
}

/* Font Faces */

/* Variable Fonts */
@font-face {
  font-display: swap;
  font-family: "Plus Jakarta Sans";
  src:
    url("/assets/fonts/PlusJakartasans-VariableFont_wght.ttf") format("ttf")
      tech("variations"),
    url(/assets/fonts/PlusJakartasans-VariableFont_wght.ttf)
      format("ttf-variations");
  font-style: normal;
  font-weight: 100 900;
}

@font-face {
  font-display: swap;
  font-family: "Plus Jakarta Sans";
  font-style: normal;
  font-weight: 400;
  src:
    url("/assets/fonts/plus-jakarta-sans-v8-latin-regular.woff2")
      format("woff2"),
    url("/assets/fonts/plus-jakarta-sans-v8-latin-regular.woff") format("woff"),
    url("/assets/fonts/plus-jakarta-sans-v8-latin-regular.ttf")
      format("truetype");
}

@font-face {
  font-display: swap;
  font-family: "Plus Jakarta Sans";
  font-style: normal;
  font-weight: 500;
  src:
    url("/assets/fonts/plus-jakarta-sans-v8-latin-500.woff2") format("woff2"),
    url("/assets/fonts/plus-jakarta-sans-v8-latin-500.woff") format("woff"),
    url("/assets/fonts/plus-jakarta-sans-v8-latin-500.ttf") format("truetype");
}

@font-face {
  font-display: swap;
  font-family: "Plus Jakarta Sans";
  font-style: normal;
  font-weight: 800;
  src:
    url("/assets/fonts/plus-jakarta-sans-v8-latin-800.woff2") format("woff2"),
    url("/assets/fonts/plus-jakarta-sans-v8-latin-800.woff") format("woff"),
    url("/assets/fonts/plus-jakarta-sans-v8-latin-800.ttf") format("truetype");
}

/* Resets */
* {
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-family: var(--ff-plus-jakarta-sans-serif);
  scroll-behavior: smooth;
}

p,
h1,
h2,
h3,
h5,
h5,
h6 {
  overflow-wrap: break-word;
}

img {
  height: auto;
  max-width: 100%;
  display: var(--block);
}

body {
  width: 100%;
  min-height: 100vh;
  text-rendering: optimizeSpeed;

  /* Both these properties only work on MacOs. */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* Both these properties only work on MacOs */

  background-color: var(--very-light-grayish-blue);

  /* Might be removed later, but this will help align the notification page to the center. */
  display: var(--flex);
  justify-content: center;
  align-items: center;
}

button {
  appearance: none;
  background: none;
  border: none;
}

a {
  text-decoration: none;
}

div[class="notification--page"] {
  background-color: var(--white);
  padding-block-start: var(--space-14);
  padding-block-end: 2.81rem;
  padding-inline: var(--space-13);
}

div[class="notification--page"] > div[class="notification--page_actions"] {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: var(--gap-15);
}

p[class="notification--page_amount"] {
  color: var(--very-dark-blue);
  font-weight: var(--fw-800);
  font-size: var(--fs-2);
  line-height: var(--line-height-normal);
  position: relative;
}

p[class="notification--page_amount"] > span {
  display: var(--inline-block);
  background-color: var(--blue);
  color: var(--white);
  padding-inline: var(--gap-6_25);
  font-size: var(--fs-1);
  margin-inline-start: var(--space-3);
  border-radius: var(--radius-3);
  padding-block: 0.06rem;
  transform: translateY(-0.125rem);
}

.notification--page_removed-btn {
  color: var(--dark-grayish-blue);
  font-size: var(--fs-1);
  font-weight: var(--fw-500);
  cursor: pointer;
  font-feature-settings:
    "clig" off,
    "liga" off;
  transition: color 0.4s ease-in;
  line-height: var(--line-height-normal);
}

@media (any-hover: hover) {
  .notification--page_removed-btn:hover {
    color: var(--blue);
  }
}

div[class~="notification--page_activity"]:not(:last-child) {
  margin-bottom: var(--gap-6_25);
}

div[class="notification--page"] > div[class~="notification--page_activity"] {
  display: var(--flex);
  gap: 0.81rem;
  padding-inline: 1rem;
  padding-block: 1rem;
}

div:is(.unread, .read) > img {
  width: 2.8125rem;
  height: 2.8125rem;
}

div[class~="unread"] {
  background-color: var(--very-light-grayish-blue);
}

div[class~="unread"] p[class="notification--page_info"]::after {
  content: "";
  display: inline-block;
  position: absolute;
  width: 0.5rem;
  height: 0.5rem;
  background-color: var(--red);
  border-radius: 50%;
  transform: translate(0.38rem, 0.49rem);
}

img[alt="a chess image"] {
  margin-left: auto;
}

img[alt="a chess image"]:hover {
  cursor: pointer;
}

.notification--page_info {
  line-height: var(--line-height-normal);
  font-weight: var(--fw-500);
  font-size: var(--fs-1);
  margin-bottom: 0.19rem;
  color: var(--dark-grayish-blue);
  font-feature-settings:
    "clig" off,
    "liga" off;
  position: relative;
}

.follower {
  color: var(--very-dark-blue);
  font-weight: var(--fw-800);
  font-feature-settings:
    "clig" off,
    "liga" off;
  transition: color 0.4s ease-in;
}

@media (any-hover: hover) {
  .follower:hover {
    color: var(--blue);
  }
}

.community {
  color: var(--blue);
  font-weight: var(--fw-700);
}

.notification--page_timeline {
  color: var(--grayish-blue);
  font-size: var(--fs-1);
}

.notification--page__private-message {
  color: var(--dark-grayish-blue);
  font-weight: var(--fw-500);
  font-size: var(--fs-1);
  font-weight: var(--fw-500);
  margin-top: 0.75rem;
  padding-block: 1rem;
  padding-inline: 1rem;
  border: 1px solid var(--very-light-grayish-blue);
  border-radius: var(--radius-3);
  cursor: pointer;
  background-color: var(--white);
  transition: background-color 0.4s ease-in;
}

/* For devices that support hover features */
@media (any-hover: hover) {
  .notification--page__private-message:hover {
    background-color: var(--light-grayish-blue-one);
  }
}

/* Handling desktop devices */

@media only screen and (min-width: 64em) {
  .notification--page_info {
    margin-bottom: 0.25rem;
  }

  .notification--page__private-message {
    margin-top: 0.81rem;
    padding-inline: 1.25rem;
    padding-block-start: 1.06rem;
    padding-block-end: 1.25rem;
  }

  div[class="notification--page"] {
    background-color: var(--white);
    border-radius: var(--radius-6);
    box-shadow: 0px 20px 60px 0px rgba(73, 97, 168, 0.05);
    width: max(51%, 730px);
    margin-inline: auto;
    padding-block-end: 1.06rem;
  }
}
